#frameSidebar{
    position: fixed;
    inset: 0;
    background-color: rgba(50, 50, 50, 0.4); 
    width: 100%;
    height: 100%;
    z-index: 1001;
}
.sidebarMenu{
    background-color: white;
    position: absolute;
    width: 100%;
    max-width: 534px;
    height: 100%;
    right: 0;
    bottom: 0;
    animation: findIn 0.4s ease;
    padding: 0.8rem;
    
}
.sidebarMenu .myAccount{
    margin: 8px 0;
}
.sidebarMenu .listNav{
    width: 100%;
    display: flex; 
    flex-wrap: wrap;
    
}
.sidebarMenu .listNav ul{
    color: blue;
    width: 24%;
    margin: 0.5%;
}
.sidebarMenu .listNav ul li{
    border: 1px solid #dcdcdc;
    padding: 8px;
    width: 100%;
    text-align: center;

}
/* .sidebarMenu ul{
    padding: 10px;
} */


@keyframes findIn {
    0%{
        transform: translateX(100%);
    }
    100%{
        transform: translateX(0);
    }
}